import { toast } from "react-toastify";
import {FRONTEND_URL,URL} from "./constants"
export function getAssetURL(id) {
  if (!id) return null;
  return `${URL}/assets/${id}`;
}
export const encode = (str) =>
    encodeURIComponent(str)
    // .replaceAll("%", '%25')
        .replace(/\-/g, '%2D')
        .replace(/\_/g, '%5F')
        .replace(/\./g, '%2E')
        .replace(/\!/g, '%21')
        .replace(/\~/g, '%7E')
        .replace(/\*/g, '%2A')
        .replace(/\'/g, '%27')
        .replace(/\(/g, '%28')
        .replaceAll("&", '%26')
        .replaceAll("#", '%23')
        .replace(/\)/g, '%29');

export const decode = (str) =>
decodeURIComponent(
    str
        .replace(/\\%2D/g, '-')
        .replace(/\\%5F/g, '_')
        .replace(/\\%2E/g, '.')
        .replace(/\\%21/g, '!')
        .replace(/\\%7E/g, '~')
        .replace(/\\%2A/g, '*')
        .replace(/\\%27/g, "'")
        .replace(/\\%28/g, '(')
        .replaceAll('&amp;', '&')
        .replaceAll("%23", '#')
        .replaceAll("&quot;", '"')
        .replace(/\\%29/g, ')')
        // .replaceAll(" % ", '%')
);
{/* <strong> ${name}</strong>, */}
export async function SendMailService({ url, token, name, emailTo,from }) {
  var msg = {
      to: emailTo,
      from: from,
      subject: 'Your login verification OTP',
      name: name,
      html: `<div>
      <p>Hi</p>
      <p>Your login verification OTP is <strong> ${token}</strong>.</p>
      <p>Click here to  <a href=${FRONTEND_URL}/verification>Verification </a></p>
      
      <div>`
  };
  await fetch(url, {
      method: 'POST',
      mode: 'cors',
      cache: 'no-cache',
      credentials: 'same-origin',
      headers: {
          'Content-Type': 'application/json'
      },
      redirect: 'follow',
      referrerPolicy: 'no-referrer',
      body: JSON.stringify(msg)
  }).then(e => toast.success('we sent you a secure password to your mail')
  ).catch(e => toast.error('Try again some other time'));
  return "";
}